* {
    padding: 0;
    margin: 0;
    font-family: 'Pixelated MS Sans Serif', sans-serif;
}

body {
    background-color: #008080;
}

.app {
    display: flex;
    width: 100vw;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.window {
    background-color: white;
    padding: 2px;
    min-width: 620px;
    width: 66vw;
    margin-top: 0.5rem;
}

.window-title {
    display: flex;
    background: linear-gradient(to right, #000181, #1081ce);
    color: #f0f0f0;
    align-items: center;
    padding: 2px;
}

.window-icon {
    width: 16px;
    height: 16px;
    margin-right: 2px;
}

.header-table {
    width: 100%;
}

.header-row {
    width: 100%;
    display: flex;
    justify-content: left;
    align-items: center;
}

.cover-art {
    min-width: 128px;
    width: 300px;
    margin: 16px 16px 0 16px
}

.maker {
    margin-bottom: 1rem;
    margin-left: 1px;
}

.description {
    margin-bottom: 1rem;
}

.game-info {
    padding-right: 16px;
}

.controls {
    display: flex;
    justify-content: right;
}

.controls  button {
    margin-left: 0.5rem;
    background-color: #c0c0c0;
    border-top: 1px solid white;
    border-left: 1px solid white;
    border-right: 1px solid black;
    border-bottom: 1px solid black;
    width: 100px;
}

.controls  button:active {
    border-top: 1px solid black;
    border-left: 1px solid black;
    border-right: 1px solid white;
    border-bottom: 1px solid white;
}

::-webkit-scrollbar {
    display: none;
}